import React from 'react';
import Card from './card';


function Home(props) {

    return (

        <div className="container">
            <div className="row">

            {
                props.shoes.map((a,i)=>{
                return <Card shoes={props.shoes[i]} key={i} />
                })
            }
            
            </div>
        </div>
    )
};

export default Home;